.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 5;
  transition: opacity var(--transition-prop), visibility var(--transition-prop);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.main-button-filled {
  color: var(--text-color_dark);
  background-color: var(--accent-color);
  height: 42px;
  transition: background-color var(--transition-prop), color var(--transition-prop);
  border: none;
  border-radius: 10px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 129%;
}

.main-button-filled:hover {
  background-color: var(--mdl-btn-color_hover);
}

.main-button-filled:focus {
  background-color: var(--mdl-btn-color_hover);
  color: var(--text-color_light);
  outline: none;
}

.main-button-filled:disabled {
  background-color: #ababab;
}

.main-button-empty {
  color: var(--text-color_dark);
  height: 42px;
  border: 2px solid var(--accent-color);
  transition: border var(--transition-prop), color var(--transition-prop);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 129%;
}

.main-button-empty:hover {
  border: 2px solid var(--mdl-btn-color_hover);
}

.main-button-empty:focus {
  border: 2px solid var(--mdl-btn-color_hover);
  color: var(--text-color_light);
  outline: none;
}

.main-button-empty:disabled {
  border: 2px solid #ababab;
}

